<template>
	<div id="wallet">
		<!--  -->
		<el-drawer title="" :size="drawerSize" custom-class="mdrawer" :visible.sync="drawer" @close="setPageScro">
			<div class="walletbox1">
				<h2 class="ptit1"></h2>
				<p class="pti2">1</p>
				<div class="mbflex box1">
					<input type="text" v-model="waddr" placeholder="" class="ipu1" />
					<button type="button" class="btntj dwr" @click="submitForm1(waddr)"></button>
				</div>
				<p class="pti2">2</p>
				<button type="button" class="btntj" @click="submitForm2()"></button>
			</div>
		</el-drawer>
		<!--  -->
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'
	
	export default {
		name: 'wallet',
		data() {
			return {
				drawerSize:'400px', //
				drawer: false, //
				waddr: '', //
			}
		},
		computed: mapState(["langType","isLogin","isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld","changePageOld"]),
			
			//
			showAlert() {
				let that = this;
				that.drawer = true;
				that.changePageOld(true); //
			},
			
			//
			submitForm1(waddr) {
				var that = this;
				that.$mpublic.isRequired(waddr).then(() => {
					console.log(waddr);
					that.drawer = false;
					that.uwallet = waddr;
					that.$message.success("");
					that.changePageOld(false); //
					that.$emit('setWaddr',waddr);
				}, err => {
					that.$message.warning("");
				})
			},
			
			//
			submitForm2() {
				var that = this;
				const loading = this.$loading({
					lock: true,
					text: '...',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.6)'
				});
				setTimeout(() => {
					loading.close();
					let waddr = "0xEBe0HOWERHjda802C83f5"
					that.drawer = false;
					that.uwallet = waddr;
					that.$message.success("");
					that.$emit('setWaddr',waddr);
					that.changePageOld(false); //
				}, 2000);
			},
			
			//
			setPageScro(){
				var that = this;
				that.changePageOld(false); //
			}
			
		}
	}
</script>

<style>
	
	.walletbox1 {
		padding: 1rem 5%;
	}
	
	.walletbox1 .ptit1 {
		text-align: center;
		font-size: 2rem;
		margin-bottom: 5rem;
	}
	
	.walletbox1 .pti2 {
		font-size: 1.2rem;
		margin: 5rem auto 2rem;
	}
	
	.walletbox1 .btntj {
		width: 100%;
		height: 4rem;
		line-height: 4rem;
		color: #FFFFFF;
		border-radius: 5px;
		margin: 1rem auto;
		display: block;
		background: #000;
		font-size: 1.2rem;
		cursor: pointer;
	}
	
	.walletbox1 .box1 {
		height: 4rem;
		overflow: hidden;
		border: 1px solid #000000;
		border-radius: 5px;
	}
	
	.walletbox1 .box1 .ipu1 {
		height: 4rem;
		line-height: 4rem;
		padding: 0 12px;
		flex-grow: 4;
	}
	
	.walletbox1 .box1 .dwr {
		flex: 0 0 8rem;
	}
	
	.el-loading-spinner i {
		font-size: 3rem;
		color: #FFFFFF;
	}
	
	.el-loading-spinner .el-loading-text {
		color: #FFFFFF;
		margin-top: 8px;
	}
	
</style>
