export default {
  //(cptxt=)
  copyFont(cptxt) {
    return new Promise((resolve, reject) => {
      var oInput = document.createElement('input');
      oInput.value = cptxt;
      document.body.appendChild(oInput);
      oInput.select(); // 
      document.execCommand("Copy"); // 
      oInput.className = 'oInput';
      oInput.style.display = 'none';
      resolve();
    })
  },
  
  //
  isRequired(sname) {
    return new Promise((resolve, reject) => {
      if (sname == '' || sname == null || !sname) {
        reject();
      } else {
        resolve(); //
      }
    })
  },
  
  //
  isTel(tel) {
    return new Promise((resolve, reject) => {
      let myreg = /^(((1[0-9]{2}))+\d{8})$/;
      if (tel.length != 11 || !myreg.test(tel)) {
        reject();
      } else {
        resolve(tel); //
      }
    })
  },
  

  // ()
  isNum(text) {
    var re = /^[0-9]*$/; //
    return new Promise((resolve, reject) => {
      if (re.test(text) && text > 0 ) {
        resolve();
      } else {
        reject();
      }
    })
  },
  
  // (2)
  isNumDist(text) {
    var re = /\d+(\.\d{0,2})?/; //
    return new Promise((resolve, reject) => {
      if (re.test(text) && text > 0 ) {
        resolve();
      } else {
        reject();
      }
    })
  },
  
  //
  omitAddr(addr){
	  let addr1 = addr.split('');
	  let addr2 = [];
	  let addr3 = [];
	  for (var i = 0; i < addr1.length; i++) {
	  	if(i < 6){
			addr2.push(addr1[i]);
		}
		if(i > addr1.length - 7){
			addr3.push(addr1[i]);
		}
	  }
	  let addr4 = addr2.join('') + '...' + addr3.join('');
	  return addr4;
  }
  
}
