					img: require('../../static/img/ban_05.jpg'),
					name: "",
					title: "",
					ifan: false, //
					iview: false, //
				}, {
					img: require('../../static/img/pic_05.jpg'),
					name: "",
					title: "",
					ifan: true, //
					iview: true, //
				}, {
					img: require('../../static/img/pic_01.jpg'),
					name: "scenery",
					title: "",
					ifan: false, //
					iview: false, //
				}, {
					img: require('../../static/img/pic_03.jpg'),
					name: "Smith",
					title: "",
					ifan: false, //
					iview: true, //
				}, {
					img: require('../../static/img/ban_01.jpg'),
					name: "Madonna",
					title: "",
					ifan: true, //
					iview: false, //
				}], //
			}
		},

		components: {
			mHeader,
			mFooter,
			Wallet
		},
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			let csid = that.$route.params.id; // id
			that.csid = csid;
			console.log(csid);
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 300);
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			},

			//
			copyFont(cptxt) {
				var that = this;
				that.$mpublic.copyFont(cptxt).then(() => {
					that.$message.success("");
				});
			},

			//
			handleClick(idx) {
				var that = this;
				that.ctabid = idx;
			},

			//-
			showAlert() {
				let that = this;
				that.$refs.mwallet.showAlert(true);
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>

