<script>
	import International from '../lib/international.js' //
	import mHeader from '@/components/mheader' //
	import mFooter from '@/components/mfooter' //
	import {
		shelvesImg,
		buyImg,
		transferImg,
		getImgLink,
		getAccount
	} from '@/util/myconnect'
	import {
		mapMutations,
		mapState
	} from 'vuex'

	var Web3 = require("web3");
	export default {
		data() {
			return {
				International,
				clink: 1, //
				ishowbg: false, //
				loading: true, //
				csid: "", //
				sid: "", //id
				sprice: "", //
				visible1: false, //
				visible2: false, //
				cvsrc: require('../../static/video/v2.mp4'),
				creatImg: require('../../static/img/bg1.gif'),
				activeName: "first", //tab1
				activeName1: "history", //tab2
				hislist: [{
					img: "https://file.cryptoart.ai/avatar/2021/3/17/SMOOCHIE-PROFILE-PIC-CRYPTOART_1618671043194.gif?x-oss-process=image/resize,m_lfit,w_128",
					date: "2019-4-20 15:06",
					addr1: "Edition Created",
					addr2: "0xEBe05BfAFa13Ca614C1310944F4f4121AEAC83f5",
				}, {
					img: "https://file.cryptoart.ai/avatar/2021/6/20/20210325_124434_1626788244768.gif?x-oss-process=image/resize,m_lfit,w_128",
					date: "2019-4-19 21:52",
					addr1: "Edition Created",
					addr2: "0xEBe05BfAFa13Ca614C1310944F4f4121AEAC83f5",
				}], //
				goodslist: [{
					img: require('../../static/video/v1.mp4'),
					type: 1, //1=2=
					user_head: "https://img0.baidu.com/it/u=680725,648018937&fm=26&fmt=auto",
					user_name: "Creator",
					user_name1: "Boss",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#484848",
					price1: 0.1,
					price2: 290.59,
					iend: false, //
				}, {
					img: require('../../static/video/v2.mp4'),
					type: 1, //1=2=
					user_head: "https://img0.baidu.com/it/u=1517838256,1457572322&fm=26&fmt=auto",
					user_name: "Creator",
					user_name1: "Serenity",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#281372",
					price1: 0.2,
					price2: 581.17,
					iend: true, //
				}, {
					img: require('../../static/img/pic_01.jpg'),
					type: 2, //1=2=
					user_head: "https://file.cryptoart.ai/avatar/2021/3/17/SMOOCHIE-PROFILE-PIC-CRYPTOART_1618671043194.gif?x-oss-process=image/resize,m_lfit,w_128",
					user_name: "Creator",
					user_name1: "",
					user_name2: "@peajey",
					title: "LIST PRICE",
					bgcol: "#07575f",
					price1: 0.1,
					price2: 290.59,
					iend: false, //
				}, {
					img: require('../../static/img/pic_03.jpg'),
					type: 2, //1=2=
					user_head: "https://file.cryptoart.ai/avatar/2021/6/20/20210325_124434_1626788244768.gif?x-oss-process=image/resize,m_lfit,w_128",
					user_name: "Creator",
					user_name1: "",
					user_name2: "@yangxin",
					title: "LIST PRICE",
					bgcol: "#452b3e",
					price1: 0.1,
					price2: 290.59,
					iend: true, //
				}], //

				muid: 1, //ID
				ctabid: 0, //
				tablist: ['', '', ''],
				price1: "",
				price2: "",
				mdatetime1: "", //
				ImgAddress: "0x6DcFE8c59b83b7e7D1F2a0FE9c1F2d9dec53db0c", //
				addrTo: "", //
				userBagAddress: "", //
				imgLink: "", //
				ispros: false, //
				cpros: 1, //
				ispros1: false, //1
				cpros1: 1, //
				ispros2: false, //2
				cpros2: 1, //
			}
		},

		components: {
			mHeader,
			mFooter
		},
		created() {
			let that = this;
			that.myAccount();
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			let cid = that.$route.params.id; // id
			that.sid = cid; // ID
			that.mgetImgLink(cid);
			let mcastlist = that.mcastlist;
			for (var i = 0; i < mcastlist.length; i++) {
				if (cid == mcastlist[i].id) {
					that.csid = i;
					that.sprice = mcastlist[i].price; //
				}
			}
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 500);
			// console.log(that.mcastlist);
		},
		computed: mapState(["langType", "isLogin", "isfixed", "mcastlist"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld", "changeCastListOld"]),

			//1
			changeSer1() {
				let that = this;
				that.visible1 = false;
			},

			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			},

			//
			handleClick(idx) {
				this.ctabid = idx;
			},

			//
			onSubmit(price1, price2, mdatetime1) {
				let that = this;
				var reg = /\d+(\.\d{0,2})?/; //
				let ctabid = that.ctabid;
				let userBagAddress = that.userBagAddress; //
				let mcastlistOld = that.mcastlist; //
				let sid = that.sid;
				let csid = that.csid;
				let sprice = 0;
				if (ctabid == 2) {
					if (!reg.test(price2)) {
						that.$message.warning("");
						that.price2 = "";
						return false;
					}
					sprice = price2;
				} else {
					if (!reg.test(price1)) {
						that.$message.warning("");
						that.price1 = "";
						return false;
					}
					sprice = price1;
				}
				let mnumber = Web3.utils.toWei(sprice, 'ether');
				that.$mpublic.isRequired(mdatetime1).then(() => {
					that.ispros = true;
					that.ispros1 = false;
					that.ispros2 = false;
					let stime = mdatetime1[0] / 1000;
					let etime = mdatetime1[1] / 1000;
					shelvesImg(sid, mnumber, stime, etime, function(res) {
						console.log(res);
						if (res == '4444') {
							that.cpros = 2;
						}
						if (res && res.status) {
							that.$message.closeAll();
							for (var i = 0; i < mcastlistOld.length; i++) {
								if (sid == mcastlistOld[i].id) {
									mcastlistOld[i].price = sprice;
									mcastlistOld[i].stime = stime;
									mcastlistOld[i].etime = etime;
									mcastlistOld[i].type = ctabid;
									mcastlistOld[i].isup = true;
								}
							}
							that.changeCastListOld(mcastlistOld); //
							that.$message.success("");
							window.location.reload();
							that.visible1 = false;
							that.ispros = false;
							that.cpros = 1;
						} 
						
						if(res.code == -4001) {
							that.ispros = false;
							that.cpros = 1;
						}
					})
				}, err => {
					that.$message.warning("");
				})
			},

			//
			btnBuyImg() {
				let that = this;
				let mcastlistOld = that.mcastlist; //
				let sid = that.sid;
				let csid = that.csid;
				let sprice = that.sprice;
				let mnumber = Web3.utils.toWei(sprice, 'ether');
				let mnumber1 = Web3.utils.numberToHex(mnumber);
				let userBagAddress = that.userBagAddress; //
				that.ispros = false;
				that.ispros1 = true;
				that.ispros2 = false;
				buyImg(sid, mnumber1, function(res) {
					console.log(res);
					if (res == '4444') {
						that.cpros1 = 2;
					}
					if (res && res.status) {
						that.$message.closeAll();
						for (var i = 0; i < mcastlistOld.length; i++) {
							if (sid == mcastlistOld[i].id) {
								mcastlistOld[i].price = sprice;
								mcastlistOld[i].isell = true;
								mcastlistOld[i].isup = false; //
								mcastlistOld[i].uaddr = userBagAddress;
							}
						}
						that.changeCastListOld(mcastlistOld); //
						that.$message.success("");
						window.location.reload();
						that.ispros1 = false;
						that.cpros1 = 1;
					}
					
					if(res.code == -4001) {
						that.ispros1 = false;
						that.cpros1 = 1;
					}
				})
			},

			//
			onSubmitTrans(userBagAddress, addrTo, sid) {
				let that = this;
				let mcastlistOld = that.mcastlist; //
				let csid = that.csid;
				that.$mpublic.isRequired(userBagAddress).then(() => {
					that.ispros2 = true;
					that.ispros = false;
					that.ispros1 = false;
					transferImg(userBagAddress, addrTo, sid, function(res) {
						console.log(res);
						if (res == '4444') {
							that.cpros2 = 2;
						}
						if (res && res.status) {
							that.$message.closeAll();
							for (var i = 0; i < mcastlistOld.length; i++) {
								if (csid == i) {
									mcastlistOld[i].uaddr = addrTo;
								}
							}
							that.changeCastListOld(mcastlistOld); //
							that.$message.success("");
							window.location.reload();
							that.visible2 = false;
							that.ispros2 = false;
							that.cpros2 = 1;
						}
						
						if(res.code == -4001) {
							that.ispros2 = false;
							that.cpros2 = 1;
						}
					})
				}, err => {
					that.$message.warning("");
				})
			},

			myAccount() {
				//
				let that = this;
				getAccount().then((res) => {
					if (res.length > 0) {
						that.userBagAddress = res[0];
					}
				});
			},

			//nft
			mgetImgLink(sid) {
				let that = this;
				getImgLink(sid).then((res) => {
					if (res.length > 0) {
						that.imgLink = res;
					}
				});
			},

			//
			copyFont(cptxt) {
				var that = this;
				that.$mpublic.copyFont(cptxt).then(() => {
					that.$message.success("");
				});
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>
