<template>
	<div id="cart">
		<mHeader :clink='clink' :ishow="ishowbg"></mHeader>
		<div class="cartbox1">
			<h2 class="p1">THE OPENART.AI TOKEN--$CART</h2>
			<img src="../../static/img/logo.png" class="img1">
			<p class="p2"></p>
			<div class="mbflex p3">
				<i class="iconfont icon-ethereum dic1"></i>
				<img src="../../static/img/dic_06.png" class="img2">
			</div>
		</div>
		<div class="allbox-hd cartbox2">
			<div class="dl clearfloat" v-for="(item,index) in introlist" :key="index">
				<video class="mvid1" autoplay="autoplay" muted loop="loop">
					<source :src="item.vsrc" type="video/mp4">
					</source>
				</video>
				<div class="pbox">
					<p class="p1">{{item.title}}</p>
					<div class="p2" v-html="item.cont"></div>
				</div>
			</div>
		</div>

		<!--  -->
		<mFooter></mFooter>
		<!--  -->

		<!--  -->
		<div class="gotop">
			<el-backtop></el-backtop>
		</div>
		<!--  -->

	</div>
</template>


<script>
	import International from '../lib/international.js' //
	import mHeader from '@/components/mheader' //
	import mFooter from '@/components/mfooter' //
	import {mapMutations,mapState} from 'vuex'
	export default {
		data() {
			return {
				International,
				clink: 3, //
				ishowbg: false, //
				loading: false, //
				introlist: [{
					vsrc: require('../../static/video/v4.mp4'),
					title: "OpenArt",
					cont: "OpenArt<br>OpenArt<br>OpenArt<br>IP",
					btxt: "",
					link: "",
				}, {
					vsrc: require('../../static/video/v5.mp4'),
					title: "OpenArtBSC",
					cont: "ERC-721ERC-1155",
					btxt: "",
					link: "",
				}, {
					vsrc: require('../../static/video/v6.mp4'),
					title: "OpenArt",
					cont: "1. IP+Openart<br>2. <br>3. OpenArt<br>4. +IP",
					btxt: "",
					link: "",
				}],
			}
		},

		components: {
			mHeader,
			mFooter
		},
		computed: mapState(["langType","isLogin","isfixed"]),
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			window.addEventListener("scroll", this.handleScroll);
			setTimeout(() => (that.loading = false), 500);
		},
		methods: {
			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			}
		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>
