<template>
	<div id="rupublish">
		<mHeader :clink='clink' :ishow="ishowbg"></mHeader>
		<!-- - -->
		<div class="allbox-hd">
			<el-form ref="form">
				<div class="upublish">
					<el-upload class="dimg" :data="imgData" action="http://cqcd.duckdns.org:5000/api/v1/upload"
						:show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
						:on-error="handleAvatarError" drag>
						<el-image v-if="imageUrl" :src="imageUrl" fit="cover" class="img1"></el-image>
						<el-image v-else :src="upbg" fit="cover" class="img1"></el-image>
					</el-upload>
					<div class="mbflex put">
						<p class="p1"></p>
						<el-input v-model="title" placeholder="" class="dwr ipu1"></el-input>
					</div>
					<div class="mbflex put">
						<p class="p1"></p>
						<el-input type="textarea" v-model="intro" placeholder="" class="dwr ipu1 mare1"></el-input>
					</div>
					<button type="button" class="btntj trans5" @click="onSubmit(imageUrl,title,intro)"></button>
				</div>
			</el-form>
		</div>

		<!--  -->
		<mFooter></mFooter>
		<!--  -->
		
		<!--  -->
		<div class="mtipbox trans5" :class="{'act': ispros}">
			<div class="mbflex box1">
				<div class="dl">
					<div class="bg1"></div>
					<p class="p1"></p>
				</div>
				<div class="dl">
					<div class="bg1" :class="{'act': cpros==1}">
						<div class="bg2"></div>
					</div>
					<p class="p1"></p>
				</div>
				<div class="dl">
					<div class="bg1" :class="{'act': cpros==2}">
						<div class="bg2"></div>
					</div>
					<p class="p1"></p>
				</div>
			</div>
		</div>
		<!--  -->

	</div>
</template>

<script>
	import International from '../lib/international.js' //
	import mHeader from '@/components/mheader' //
	import mFooter from '@/components/mfooter' //
	import {
		setCastImg,
		getAccount
	} from '@/util/myconnect'

	import {
		mapMutations,
		mapState
	} from 'vuex'
	var Web3 = require("web3");
	export default {
		name: 'rupublish',
		data() {
			return {
				International,
				userBagAddress: "", //
				clink: 5, //
				ishowbg: false, //
				title: "", //
				intro: "", //
				imageUrl: "", //
				upbg: require('../../static/img/upbg.jpg'),
				imgData: {
					image:"",
					filename:"",
				}, //
				ispros: false, //
				cpros: 1, //
			}
		},

		components: {
			mHeader,
			mFooter
		},
		created() {
			let that = this;
			document.documentElement.scrollTop = 0;
			that.myAccount();
		},
		mounted() {
			let that = this;
			// let ar1=[];
			// that.changeCastListOld(ar1); //
			window.addEventListener("scroll", this.handleScroll);
		},
		computed: mapState(["langType", "isLogin", "isfixed", "mcastlist"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld", "changeCastListOld"]),

			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			},
			
			//
			handleAvatarSuccess(res, file) {
				let that = this;
				console.log(res);
				that.imageUrl = res;
			},
			
			//
			handleAvatarError(err, file, fileList){
				let that = this;
				this.$message.error("");
			},
			
			//
			beforeAvatarUpload(file) {
				let that = this;
				let imgData = that.imgData;
				const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isJPG) {
					this.$message.error('pngjpgjpeg!');
				}
				if (!isLt2M) {
					this.$message.error(' 2MB!');
				}
				imgData.image = file;
				imgData.filename = file.name;
				that.imgData = imgData;
			},

			//
			handleClick(idx) {
				this.ctabid = idx;
			},

			//
			onSubmit(imageUrl, title, intro) {
				let that = this;
				var reg = /\d+(\.\d{0,2})?/; //
				let userBagAddress = that.userBagAddress; //
				let mcastlistOld = that.mcastlist; //
				that.$mpublic.isRequired(imageUrl).then(() => {
					that.$mpublic.isRequired(title).then(() => {
						that.ispros = true;	
						setCastImg(userBagAddress, imageUrl, function(res) {
							console.log(res);	
							if(res == '4444'){
								that.cpros = 2;
							}
							if (res && res.status) {
								that.$message.closeAll();
								let toenkId = res.logs[0].topics[3];
								let imgId = Web3.utils.hexToNumber(toenkId);
								mcastlistOld.unshift({
									id: imgId,
									img: imageUrl, //
									title: title, //
									intro: intro, //
									price: 0, //
									stime: "", //
									etime: "", //
									type: 0, //'0=', '1=', '2='
									isup: false, //
									isell: false, //
									uaddr: userBagAddress, //
									uaddrCast: userBagAddress, //
								});
								that.changeCastListOld(mcastlistOld); //
								that.$message.success("");
								window.location.reload();
								that.ispros = false;
								that.cpros = 1;
							}
							
							if(res.code == -4001){
								that.ispros = false;
								that.cpros = 1;
							}
						})
					}, err => {
						that.$message.warning("");
					})
				}, err => {
					that.$message.warning("");
				})
			},

			myAccount() {
				//
				let that = this;
				getAccount().then((res) => {
					if (res.length > 0) {
						that.userBagAddress = res[0];
					}
				});
			},

		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>

