<template>
	<div id="home">
		<mHeader :clink='clink' :ishow="ishowbg"></mHeader>
		<div class="allbox-hd">
			<div class="homebox1">
				<el-row :gutter="20" class="mbflex box1">
					<el-col :sm="24" :md="9">
						<div class="pbox1">
							<p class="p1"></p>
							<p class="p2"></p>
							<router-link to="/">
								<div class="ar1">
									<p class="bg1 trans3"></p>
									<p class="bg2 trans5"></p>
									<p class="bg3 trans5"></p>
								</div>
							</router-link>
						</div>
					</el-col>

					<el-col :sm="24" :md="15">
						<el-skeleton animated :loading="loading">
							<template slot="template">
								<div class="home-solt1">
									<div class="mbflex home-solt1" v-if="icard">
										<el-skeleton-item variant="image" class="sdl bg1" />
										<el-skeleton-item variant="image" class="sdl" />
										<el-skeleton-item variant="image" class="sdl bg2" />
									</div>
									<el-skeleton-item v-else variant="image" class="sdl-sm" />
								</div>
							</template>
							<template>
								<div v-if="!loading">
									<el-carousel indicator-position="outside" class="mbanner" :interval="3200"
										height="420px" :type="icard">
										<el-carousel-item
											v-for="(item,index) in International[langType].homebox1.bannerlis"
											:key="index">
											<div class="rdl">
												<el-image :src="item.img" fit="cover" class="img1"></el-image>
												<div class="pbox trans5">
													<p class="p1 p-hide">{{item.name}}</p>
													<p class="p2 p-hide">{{item.cont}}</p>
												</div>
											</div>
										</el-carousel-item>
									</el-carousel>
								</div>
							</template>
						</el-skeleton>
					</el-col>
				</el-row>
			</div>

			<div class="rmhomebox1">
				<div class="rmpubtit">
					<div class="mbflex pti1">
						<p class="sp1"></p>
					</div>
					<p class="pti2"></p>
				</div>
				<div class="box1">
					<el-row :gutter="30">
						<el-col v-for="(item,index) in artlist" :xs="12" :sm="8" :md="4" :key="index">
							<router-link :to="'/userDetail/' + index">
								<div class="dl trans5">
									<el-image :src="item.himg" fit="cover" class="img1 trans3"></el-image>
									<p class="p1 p-hide">{{item.name}}</p>
								</div>
							</router-link>
						</el-col>
					</el-row>
				</div>
				<router-link to="/artists">
					<div class="amore">
						<p class="sp2">more</p>
						<p><i class="iconfont icon-gengduo dic1 sp1 animScals"></i></p>
					</div>
				</router-link>
			</div>
		</div>


		<div class="rmhomebox2">
			<div class="rmpubtit">
				<div class="mbflex pti1">
					<p class="sp1"></p>
				</div>
				<p class="pti2"></p>
			</div>
			<Goodslist></Goodslist>
		</div>


		<!--  -->
		<mFooter></mFooter>
		<!--  -->

	</div>
</template>

<script>
	import International from '../lib/international.js' //
	import mHeader from '@/components/mheader' //
	import mFooter from '@/components/mfooter' //
	import Goodslist from '@/components/goodslist' //
	import Login from '@/components/login' //

	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				International,
				clink: 0, //
				ishowbg: false, //
				loading: true,
				icard: "card", //
				screenWidth: document.documentElement.clientWidth, //
				iserch: false, //
				artlist: [{
					himg: "https://img0.baidu.com/it/u=680725,648018937&fm=26&fmt=auto",
					name: "shahab_mehr",
				}, {
					himg: "https://img0.baidu.com/it/u=3038751276,4078131470&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
					name: "Metamaks",
				}, {
					himg: "https://img1.baidu.com/it/u=991636858,1692507174&fm=253&fmt=auto&app=138&f=JPEG?w=501&h=500",
					name: "Winchawa",
				}, {
					himg: "https://img1.baidu.com/it/u=1810275497,1441377144&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
					name: "Winchawa",
				}, {
					himg: "https://img1.baidu.com/it/u=1532660915,458981992&fm=15&fmt=auto",
					name: "Winchawa",
				}, {
					himg: "https://img2.baidu.com/it/u=845875361,1848923256&fm=253&fmt=auto&app=138&f=JPEG?w=400&h=400",
					name: "scenery",
				}, {
					himg: "https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic.51yuansu.com%2Fpic3%2Fcover%2F03%2F12%2F65%2F5b4f1fd67d604_610.jpg&refer=http%3A%2F%2Fpic.51yuansu.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1655272712&t=3263ce618479fa7b181be691a3ca6956",
					name: "Smith",
				}, {
					himg: "https://file.cryptoart.ai/avatar/2022/1/8/crypto-5_1644311564151.png",
					name: "Madonna",
				}, {
					himg: "https://img1.baidu.com/it/u=1532660915,458981992&fm=15&fmt=auto",
					name: "Winchawa",
				}, {
					himg: "https://img2.baidu.com/it/u=845875361,1848923256&fm=253&fmt=auto&app=138&f=JPEG?w=400&h=400",
					name: "scenery",
				}, {
					himg: "https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic.51yuansu.com%2Fpic3%2Fcover%2F03%2F12%2F65%2F5b4f1fd67d604_610.jpg&refer=http%3A%2F%2Fpic.51yuansu.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1655272712&t=3263ce618479fa7b181be691a3ca6956",
					name: "Smith",
				}, {
					himg: "https://file.cryptoart.ai/avatar/2022/1/8/crypto-5_1644311564151.png",
					name: "Madonna",
				}], //
			}
		},

		components: {
			mHeader,
			mFooter,
			Goodslist,
			Login
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			that.getScreenWidth();
			window.addEventListener("scroll", this.handleScroll);
			window.addEventListener('resize', function() {
				that.screenWidth = document.body.offsetWidth;
			});
			setTimeout(() => (that.loading = false), 500);
		},
		methods: {
			//
			getScreenWidth() {
				let that = this;
				let boxw = document.body.offsetWidth;
				if (boxw < 1100) {
					that.icard = ""; //
				} else {
					that.icard = "card"; //
				}
				that.screenWidth = boxw;
			},

			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			},

			//
			showAlert() {
				let that = this;
				that.$refs.mlogin.showAlert(true);
			},

		},

		watch: {
			screenWidth: function(val) {
				//
				// console.log(val)
				let that = this;
				if (val < 1100) {
					that.icard = ""; //
				} else {
					that.icard = "card"; //
				}
			}
		},

		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>
<style>
	/*  */
	.home-solt1 {
		perspective: 300px;
		-webkit-perspective: 300px;
	}

	.home-solt1 .sdl-sm {
		width: 100%;
		height: 408px;
		margin: 2rem auto;
	}

	.home-solt1 .sdl {
		width: 370px;
		height: 408px;
		position: relative;
		z-index: 3;
	}

	.home-solt1 .bg1 {
		width: 320px;
		height: 370px;
		transform: rotateY(15deg);
		-webkit-transform: rotateY(15deg);
		transform-origin: 0 100% 0;
		z-index: 1;
	}

	.home-solt1 .bg2 {
		width: 300px;
		height: 370px;
		transform: rotateY(-15deg);
		-webkit-transform: rotateY(-15deg);
		transform-origin: 100% 0 0;
		z-index: 1;
	}
</style>
