<template>
	<div id="login">
		<!--  -->
		<div class="ms-loginbox">
			<div class="ptit1">
				<img src="../../static/img/logo.png" class="img1">
				<h2></h2>
			</div>
			<div class="box1">
				<el-tabs v-model="activeName" @tab-click="handleClick">
					<el-tab-pane label="" name="one">
						<div class="mbflex put">
							<i class="iconfont icon-zhanghao dic1"></i>
							<el-input v-model="uname" placeholder="" class="ipu1"></el-input>
						</div>
						<div class="mbflex put">
							<i class="iconfont icon-mima dic1"></i>
							<el-input class="ipu1" placeholder="" v-model="npsd" show-password></el-input>
						</div>
					</el-tab-pane>
					<el-tab-pane label="" name="two">
						<div class="mbflex put">
							<i class="iconfont icon-shouji dic1"></i>
							<el-input maxlength="11" v-model="utel" placeholder="" class="ipu1"></el-input>
						</div>
						<div class="mbflex put">
							<i class="iconfont icon-yanzhengma dic1"></i>
							<el-input maxlength="4" v-model="uyzm" placeholder="" class="ipu1"></el-input>
							<button type="button" class="btn1"></button>
						</div>
					</el-tab-pane>
				</el-tabs>
			</div>
			<div class="put box2">
				<el-checkbox v-model="icke">
					<label class="ar2" @click="showAgree"></label>
				</el-checkbox>
			</div>
			<button type="button" class="btntj" @click="submitForm(activeName)"></button>
			<div class="mbflex box2">
				<a href="" class="ar1"></a>
				<!-- <a href="" class="ar1"><span class="ar2"></span></a> -->
			</div>
		</div>
		<!--  -->

		<!-- - -->
		<div class="ms-agreement trans5" :class="{'act':ishow1}">
			<div class="allbox-hd box1">
				<h2 class="pti1"></h2>
				<div class="pbox" v-for="(item,index) in 3" :key="index">
					OpenArt<br>
					<br>
					<br>
					<br>
					<br>
					<br>
					<br>
					//()
					/
				</div>
				<button v-if="seconds > 0" type="button" class="btnclose trans5">
					<label class="sp1">({{seconds}}s)</label>
					
				</button>
				<button v-else type="button" class="btnclose act trans5" @click="ishow1 = false">
					
				</button>
			</div>
		</div>

		<!-- - -->
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	export default {
		name: 'login',
		data() {
			return {
				drawer: false, //
				activeName: 'one',
				uname: '', //
				npsd: '', //
				utel: '', //
				uyzm: '', //
				icke: false, //
				seconds: 5, //
				ishow1: false, //

			}
		},

		created() {

		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld"]),

			//
			handleClick(tab, event) {
				console.log(tab.name);
			},

			//
			submitForm(ctype) {
				let that = this;
				let uname = that.uname;
				let npsd = that.npsd;
				let utel = that.utel;
				let uyzm = that.uyzm;
				let icke = that.icke;
				if (ctype == 'one') {
					//
					that.$mpublic.isRequired(uname).then(() => {
						that.$mpublic.isRequired(npsd).then(() => {
							if (icke) {
								console.log(uname, npsd);
								that.drawer = false;
								that.changeLoginOld(true); //
								that.changePageOld(false); //
								setTimeout(function(){
									that.$router.push({name:'home'});
								},50);
							} else {
								that.$message.warning("");
							}
						}, err => {
							that.$message.warning("~");
						})
					}, err => {
						that.$message.warning("");
					})
				} else {
					//
					that.$mpublic.isTel(utel).then(() => {
						that.$mpublic.isRequired(uyzm).then(() => {
						 if (icke) {
								console.log(utel, uyzm);
								that.drawer = false;
								that.changeLoginOld(true); //
								that.changePageOld(false); //
								setTimeout(function(){
									that.$router.push({name:'home'});
								},50);
							} else {
								that.$message.warning("");
							}
						}, err => {
							that.$message.warning("");
						})
					}, err => {
						that.$message.warning("~");
						that.utel = '';
					})
				}
			},

			//
			showAgree() {
				let that = this;
				that.ishow1 = true;
				that.stimeAgree();
			},

			//
			stimeAgree() {
				let that = this;
				let cnum = that.seconds;
				let timer1 = setInterval(function() {
					if (cnum > 0) {
						cnum -= 1;
						that.seconds = cnum;
					} else {
						clearInterval(timer1);
					}
				}, 1000);
			},

			//
			setPageScro() {
				var that = this;
				that.changePageOld(false); //
			}

		}
	}
</script>

<style>
	.ms-loginbox {
		padding: 1rem 5%;
	}

	.ms-loginbox .ptit1 {
		text-align: center;
		font-size: 1.2rem;
		margin-bottom: 5rem;
	}

	.ms-loginbox .ptit1 .img1 {
		width: 80px;
		margin: 3rem auto;
	}

	.ms-loginbox .btntj {
		width: 100%;
		padding: 16px 0;
		color: #FFFFFF;
		border-radius: 5px;
		margin: 2rem auto;
		display: block;
		background: #000;
		font-size: 1.2rem;
	}

	.ms-loginbox .box1 {
		margin: 0 auto 4rem;
	}

	.ms-loginbox .box1 .put {
		padding: 2.4rem 0 1.4rem;
		border-bottom: 1px solid #d9d9d9;
		white-space: nowrap;
	}

	.ms-loginbox .box1 .dic1 {
		font-size: 1.6rem;
	}

	.ms-loginbox .box1 .ipu1 {
		flex-grow: 4;
		font-size: 1rem;
		padding: 0 1rem;
	}

	.ms-loginbox .box1 .el-input__inner {
		border: none;
		padding: 0;
	}

	.ms-loginbox .box1 .btn1 {
		padding: 10px 16px;
		border: 1px solid #BFBFBF;
		border-radius: 5px;
		cursor: pointer;
	}

	.ms-loginbox .box2 .ar1 {
		color: #5C5C5C;
	}

	.ms-loginbox .box2 .ar2 {
		color: #3aaaf9;
		cursor: pointer;
	}

	.ms-agreement {
		width: 100%;
		height: 100%;
		overflow: hidden;
		overflow-y: auto;
		background: #FFFFFF;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 2020;
		pointer-events: none;
		transform: scale(1.2);
		opacity: 0;
	}

	.ms-agreement .box1 {
		padding: 3rem 5%;
	}

	.ms-agreement .pti1 {
		text-align: center;
		margin-bottom: 2rem;
	}

	.ms-agreement .pbox {
		font-size: 1.14rem;
		line-height: 2.4rem;
		color: #333333;
	}

	.ms-agreement .btnclose {
		width: 240px;
		line-height: 3.8rem;
		margin: 3rem auto 1rem;
		background: #e3e3e3;
		display: block;
		border-radius: 50px;
		font-size: 1.2rem;
		color: #919191;
		cursor: pointer;
	}

	.ms-agreement .btnclose .sp1 {
		margin-right: 6px;
	}

	.ms-agreement .btnclose.act {
		background: #000000;
		color: #FFFFFF;
	}

	.ms-agreement.act {
		pointer-events: auto;
		transform: scale(1);
		opacity: 1;
	}
</style>
