<template>
	<div id="user">
		<mHeader :clink='4' :ishow="ishowbg"></mHeader>
		<div class="serbox1">
			<div v-for="(ritem,ridx) in tablist" :key="ridx">
				<router-link class="mbflex dli" :to="ritem.link">
					<label class="sp1">{{ritem.name}}</label>
					<i class="iconfont icon-xiangyoujiantou dic1"></i>
				</router-link>
			</div>
		</div>

		<button type="button" class="rexit" @click="setExit">
			<i class="iconfont icon-logout dic1"></i>
		</button>

	</div>
</template>

<script>
	import International from '../lib/international.js' //
	import mHeader from '@/components/mheader' //

	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				International,
				ishowbg: false, //
				tablist: [{
					id: 11,
					name: "",
					link: "/userNews/"
				}, {
					id: 14,
					name: "",
					link: "/"
				}, {
					id: 8,
					name: "",
					link: "/userInfo"
				}, {
					id: 9,
					name: "",
					link: "/userIndentify"
				}, {
					id: 10,
					name: "",
					link: "/userSet"
				}], //
			}
		},

		components: {
			mHeader
		},
		created() {
			document.documentElement.scrollTop = 0;
		},
		mounted() {
			let that = this;
			window.addEventListener("scroll", this.handleScroll);
		},
		computed: mapState(["langType", "isLogin", "isfixed"]),
		methods: {
			...mapMutations(["changeLoginOld", "changePageOld"]),

			//
			handleScroll() {
				let that = this;
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if (scrollTop > 0) {
					that.ishowbg = true; //-
				} else {
					that.ishowbg = false; //-
				}
			},

			//
			setExit() {
				let that = this;
				that.$confirm('', '?', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					that.changeLoginOld(false); //
					that.$router.push({
						path: '/'
					}); //
				}).catch(() => {});
			}


		},
		destroyed() {
			document.removeEventListener('scroll', this.handleScroll); //
		}
	}
</script>

<style>
	.serbox1{
		padding: 0 2%;
	}
	.serbox1 .dli{
		font-size: 1.24rem;
		color: #000;
		padding: 1.4rem 2%;
		border-bottom: 1px solid #f2f2f2;
	}
	
	.rexit{
		width: 100%;
		text-align: center;
		padding: 2rem;
		color: #999;
		font-size: 1.2rem;
	}
	.rexit .dic1{
		font-size: 1.2rem;
		margin: 0 4px;
	}
	
</style>
